/** Media Queries **/
table {
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
}

th, td {
  text-align: left;
  padding: 8px;
}

th {
  background-color: #001D23;
  color: #FFFFFF;
}

@media screen and (max-width: 600px) {
  table, thead, tbody, th, td, tr {
    display: block;
  }
  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  tr {
    border: 1px solid #000000;
  }
  td {
    border-bottom: 1px solid #000000;
    position: relative;
    padding-left: 50%;
  }
  td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    content: attr(data-label);
    font-weight: bold;
  }
}